$black: #232323;
$background: #cec067e8;
$gray-line: #f6f6f6;
$orange: #fe5f1e;

$container-width: 90%;

$duration: 0.55s;

$main: #2b6ca3;
$main-rgb: rgb(43, 108, 163);
$secondary: #ffde11;
$yellow: #f8e253e8;
$white: #ffffff;
$error: rgba(230, 0, 0, 0.925);

@keyframes widthAnimation {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes heightInput {
  0% {
    max-height: 0;
  }
  100% {
    max-height: 344px;
  }
}
@keyframes heightSortPopup {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}
